// contexts/ImageContext.jsx
import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';
import { getImages, uploadImage, deleteImage as apiDeleteImage } from '../api/image';

const ImageContext = createContext();

export const ImageProvider = ({ children }) => {
  const [imageList, setImageList] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchImages = useCallback(async () => {
    try {
      setLoading(true);
      const response = await getImages();
      setImageList(response.data);
    } catch (err) {
      console.error('Error fetching images:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  const addImage = async (formData) => {
    const response = await uploadImage(formData);
    setImageList((prev) => [response.data, ...prev]);
    return response.data;
  };

  const deleteImage = async (id) => {
    await apiDeleteImage(id);
    setImageList((prev) => prev.filter((img) => img.id !== id));
  };

  useEffect(() => {
    fetchImages();
  }, [fetchImages]);

  return (
    <ImageContext.Provider value={{ imageList, fetchImages, addImage, deleteImage, loading }}>
      {children}
    </ImageContext.Provider>
  );
};

export const useImages = () => useContext(ImageContext);
